.radiobutton [type="radio"]:not(:checked),
.radiobutton [type="radio"]:checked {
  position: absolute;
  left: -9999px;
  opacity: 0;
}

.radiobutton [type="radio"]:not(:checked) + label,
.radiobutton [type="radio"]:checked + label {
  position: relative;
  padding-left: 35px;
  cursor: pointer;
  display: inline-block;
  height: 25px;
  line-height: 25px;
  font-size: 1rem;
  transition: .28s ease;
  font-weight: 300;
  -khtml-user-select: none;
  user-select: none;
}

.radiobutton [type="radio"] + label:before,
.radiobutton [type="radio"] + label:after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  margin: 4px;
  width: 16px;
  height: 16px;
  z-index: 0;
  transition: .28s ease;
}

.radiobutton [type="radio"]:not(:checked) + label:before,
.radiobutton [type="radio"]:not(:checked) + label:after,
.radiobutton [type="radio"]:checked + label:before,
.radiobutton [type="radio"]:checked + label:after,
.radiobutton [type="radio"].with-gap:checked + label:before,
.radiobutton [type="radio"].with-gap:checked + label:after {
  border-radius: 50%;
}

.radiobutton [type="radio"]:not(:checked) + label:before,
.radiobutton [type="radio"]:not(:checked) + label:after {
  border: 1px solid #b7b7b7;
}

.radiobutton [type="radio"]:not(:checked) + label:after {
  transform: scale(0);
}

.radiobutton [type="radio"]:checked + label:before {
  border: 1px solid transparent;
}

.radiobutton [type="radio"]:checked + label:after,
.radiobutton [type="radio"].with-gap:checked + label:before,
.radiobutton [type="radio"].with-gap:checked + label:after {
  border: #fc1299;
}

.radiobutton [type="radio"]:checked + label:after,
.radiobutton [type="radio"].with-gap:checked + label:after {
  background-color: #fc1299;
  transform: scale(0.5);
}

.radiobutton [type="radio"]:checked + label:after {
  transform: scale(1.02);
}

.radiobutton [type="radio"].with-gap:checked + label:after {
  transform: scale(0.5);
}

.radiobutton [type="radio"].tabbed:focus + label:before {
  box-shadow: 0 0 0 10px rgba(0, 0, 0, 0.1);
}

.radiobutton [type="radio"].with-gap:disabled:checked + label:before {
  border: 2px solid #000;
}

.radiobutton [type="radio"].with-gap:disabled:checked + label:after {
  border: none;
  background-color: #000;
}

.radiobutton [type="radio"]:disabled:not(:checked) + label:before,
.radiobutton [type="radio"]:disabled:checked + label:before {
  background-color: transparent;
  border-color: #000;
}

.radiobutton [type="radio"]:disabled + label {
  color: #000;
}

.radiobutton [type="radio"]:disabled:not(:checked) + label:before {
  border-color: #000;
}

